(function () {

///////////////////////////////////////////////////////////////////////////////////////
//                                                                                   //
// packages/yasinuslu:blaze-meta/lib/template.meta.js                                //
//                                                                                   //
///////////////////////////////////////////////////////////////////////////////////////
                                                                                     //
                                                                                     // 1
Template.__checkName("MetaTags");                                                    // 2
Template["MetaTags"] = new Template("Template.MetaTags", (function() {               // 3
  var view = this;                                                                   // 4
  return Blaze.Each(function() {                                                     // 5
    return Spacebars.call(view.lookup("tags"));                                      // 6
  }, function() {                                                                    // 7
    return [ "\n    ", Spacebars.include(view.lookupTemplate("_MetaTag")), "\n  " ]; // 8
  });                                                                                // 9
}));                                                                                 // 10
                                                                                     // 11
///////////////////////////////////////////////////////////////////////////////////////

}).call(this);






(function () {

///////////////////////////////////////////////////////////////////////////////////////
//                                                                                   //
// packages/yasinuslu:blaze-meta/lib/meta.js                                         //
//                                                                                   //
///////////////////////////////////////////////////////////////////////////////////////
                                                                                     //
Meta = {                                                                             // 1
  options: {                                                                         // 2
    title: "Default Title",                                                          // 3
    suffix: "Suffix for title",                                                      // 4
    separator: " | "                                                                 // 5
  },                                                                                 // 6
                                                                                     // 7
  dict: new ReactiveDict(),                                                          // 8
                                                                                     // 9
  converters: {                                                                      // 10
    title: function(title) {                                                         // 11
      if (_.isFunction(title)) {                                                     // 12
        title = title();                                                             // 13
      }                                                                              // 14
                                                                                     // 15
      if (_.isEmpty(title)) {                                                        // 16
        return Meta.options.title || "";                                             // 17
      }                                                                              // 18
                                                                                     // 19
      if (!_.isEmpty(Meta.options.suffix)) {                                         // 20
        title = title + Meta.options.separator + Meta.options.suffix;                // 21
      }                                                                              // 22
                                                                                     // 23
      return title;                                                                  // 24
    },                                                                               // 25
                                                                                     // 26
    meta: function(property, content) {                                              // 27
      var options = _.isObject(property) ? property : {                              // 28
        name: 'property',                                                            // 29
        property: property,                                                          // 30
        content: content                                                             // 31
      };                                                                             // 32
                                                                                     // 33
      return options;                                                                // 34
    }                                                                                // 35
  },                                                                                 // 36
                                                                                     // 37
  init: function() {                                                                 // 38
    Meta.setTitle("");                                                               // 39
  },                                                                                 // 40
                                                                                     // 41
  config: function(opts) {                                                           // 42
    _.extend(Meta.options, opts.options);                                            // 43
    _.extend(Meta.converters, opts.converters);                                      // 44
  },                                                                                 // 45
                                                                                     // 46
  setVar: function(key, value) {                                                     // 47
    Meta.dict.set(key, value);                                                       // 48
  },                                                                                 // 49
                                                                                     // 50
  getVar: function(key) {                                                            // 51
    return Meta.dict.get(key);                                                       // 52
  },                                                                                 // 53
                                                                                     // 54
  set: function(property, content) {                                                 // 55
    var properties = property;                                                       // 56
    if (!_.isArray(property)) {                                                      // 57
      properties = new Array(property);                                              // 58
    }                                                                                // 59
                                                                                     // 60
    properties.forEach(function(property, key) {                                     // 61
      var meta;                                                                      // 62
      Tracker.nonreactive(function() {                                               // 63
        meta = Meta.getVar("tag") || {};                                             // 64
      });                                                                            // 65
      var m = Meta.converters.meta(property, content);                               // 66
      meta[m.property] = m;                                                          // 67
      Meta.setVar("tag", meta);                                                      // 68
    });                                                                              // 69
                                                                                     // 70
  },                                                                                 // 71
                                                                                     // 72
  unset: function(property) {                                                        // 73
    var meta;                                                                        // 74
    Tracker.nonreactive(function() {                                                 // 75
      meta = Meta.getVar("tag") || {};                                               // 76
    });                                                                              // 77
    var m = Meta.converters.meta(property);                                          // 78
    delete meta[m.property];                                                         // 79
    Meta.setVar("tag", meta);                                                        // 80
  },                                                                                 // 81
                                                                                     // 82
  unsetAll: function () {                                                            // 83
    _.each(Meta.arr(), function (item) {                                             // 84
      Meta.unset(item.property);                                                     // 85
    });                                                                              // 86
  },                                                                                 // 87
                                                                                     // 88
  setTitle: function(title) {                                                        // 89
    Meta.setVar("title", Meta.converters.title(title));                              // 90
  },                                                                                 // 91
                                                                                     // 92
  getTitle: function() {                                                             // 93
    return Meta.getVar("title");                                                     // 94
  },                                                                                 // 95
                                                                                     // 96
  arr: function() {                                                                  // 97
    var meta = Meta.getVar("tag");                                                   // 98
    return _.toArray(meta);                                                          // 99
  },                                                                                 // 100
                                                                                     // 101
  hash: function() {                                                                 // 102
    return Meta.getVar("tag");                                                       // 103
  }                                                                                  // 104
};                                                                                   // 105
                                                                                     // 106
Template.MetaTags.helpers({                                                          // 107
                                                                                     // 108
  tags: function() {                                                                 // 109
    return Meta.arr();                                                               // 110
  },                                                                                 // 111
                                                                                     // 112
  _MetaTag: function() {                                                             // 113
    var attrs = {};                                                                  // 114
    attrs[this.name] = this.property;                                                // 115
    attrs.content = this.content;                                                    // 116
    return Blaze.Template(function() {                                               // 117
      return HTML.META(attrs);                                                       // 118
    });                                                                              // 119
  }                                                                                  // 120
});                                                                                  // 121
                                                                                     // 122
Meteor.startup(function() {                                                          // 123
  Meta.init();                                                                       // 124
                                                                                     // 125
  Blaze.render(Template.MetaTags, document.head);                                    // 126
                                                                                     // 127
  Tracker.autorun(function() {                                                       // 128
    document.title = Meta.getTitle();                                                // 129
  });                                                                                // 130
});                                                                                  // 131
                                                                                     // 132
///////////////////////////////////////////////////////////////////////////////////////

}).call(this);
